import React, { useEffect, useState } from "react";

export default function Cart() {
  const [cart, setCart] = useState([]);
  const [showCheckout, setShowCheckout] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    address: "",
    card: "",
  });
  const [orderPlaced, setOrderPlaced] = useState(false);

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);
  }, []);

  const updateLocalStorage = (updatedCart) => {
    setCart(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart));
  };

  const handleIncrease = (id) => {
    const updatedCart = cart.map((item) =>
      item.id === id ? { ...item, quantity: item.quantity + 1 } : item
    );
    updateLocalStorage(updatedCart);
  };

  const handleDecrease = (id) => {
    let updatedCart = cart
      .map((item) =>
        item.id === id ? { ...item, quantity: item.quantity - 1 } : item
      )
      .filter((item) => item.quantity > 0);
    updateLocalStorage(updatedCart);
  };

  const handleRemove = (id) => {
    const updatedCart = cart.filter((item) => item.id !== id);
    updateLocalStorage(updatedCart);
  };

  const total = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handlePayment = (e) => {
    e.preventDefault();

    // Regex rules
    const nameRegex = /^[a-zA-Z\s]{3,30}$/;
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const cardRegex = /^\d{16}$/;
    const addressRegex = /.{5,}/;

    if (!nameRegex.test(formData.name)) {
      alert("Please enter a valid name (letters only, 330 characters).");
      return;
    }

    if (!emailRegex.test(formData.email)) {
      alert("Please enter a valid email address.");
      return;
    }

    if (!addressRegex.test(formData.address)) {
      alert("Please enter a valid address (at least 5 characters).");
      return;
    }

    if (!cardRegex.test(formData.card)) {
      alert("Card number must be exactly 16 digits.");
      return;
    }

    // If all regex checks pass:
    setOrderPlaced(true);
    setCart([]);
    localStorage.removeItem("cart");
  };

  return (
    <section className="container cart-section" style={{ minHeight: "75vh" }}>
      <h2 className="fw-bold mb-3"> Your Cart</h2>

      {cart.length === 0 && !orderPlaced && (
        <p className="text-muted">Cart is empty</p>
      )}

      {/* Success message */}
      {orderPlaced && (
        <div className="alert alert-success d-flex align-items-center gap-3">
          <i className="fas fa-check-circle fa-lg text-success"></i>
          <div>
            <strong>Success!</strong> Your order has been received. 
          </div>
        </div>
      )}

      {!orderPlaced && cart.length > 0 && (
        <>
          <div className="list-group mb-3">
            {cart.map((item) => (
              <div
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {/* Product Info */}
                <div className="d-flex align-items-center gap-3">
                  <img
                    src={item.image}
                    alt={item.title}
                    style={{
                      width: "60px",
                      height: "60px",
                      objectFit: "cover",
                      borderRadius: "8px",
                    }}
                  />
                  <div>
                    <h6 className="mb-1">{item.title}</h6>
                    <small className="text-muted">
                      {item.price}$  {item.quantity}
                    </small>
                  </div>
                </div>

                {/* Actions */}
                <div className="d-flex align-items-center gap-2">
                  <button
                    className="btn btn-sm btn-outline-secondary"
                    onClick={() => handleDecrease(item.id)}
                  >
                    -
                  </button>
                  <button
                    className="btn btn-sm btn-outline-secondary"
                    onClick={() => handleIncrease(item.id)}
                  >
                    +
                  </button>
                  <button
                    className="btn btn-sm btn-outline-danger"
                    onClick={() => handleRemove(item.id)}
                  >
                    <i className="fas fa-times"></i>
                  </button>
                  <span className="fw-bold">{item.price * item.quantity}$</span>
                </div>
              </div>
            ))}

            {/* Total */}
            <div className="list-group-item d-flex justify-content-between mt-2">
              <strong>Total:</strong>
              <span className="fw-bold text-success">{total}$</span>
            </div>
          </div>

          {!showCheckout && (
            <button
              className="btn btn-warning btn-lg w-100 mb-3"
              onClick={() => setShowCheckout(true)}
            >
              <i className="fas fa-credit-card me-2"></i> Proceed to Payment
            </button>
          )}

          {showCheckout && (
            <form onSubmit={handlePayment} className="card p-3 shadow-sm">
              <h5 className="mb-3">Payment Details</h5>

              <div className="mb-2">
                <label className="form-label">
                  <i className="fas fa-user me-2"></i> Name
                </label>
                <input
                  type="text"
                  name="name"
                  className="form-control"
                  required
                  value={formData.name}
                  onChange={handleChange}
                />
              </div>

              <div className="mb-2">
                <label className="form-label">Email</label>
                <input
                  type="email"
                  name="email"
                  className="form-control"
                  required
                  value={formData.email}
                  onChange={handleChange}
                />
              </div>

              <div className="mb-2">
                <label className="form-label">Address</label>
                <textarea
                  name="address"
                  className="form-control"
                  required
                  value={formData.address}
                  onChange={handleChange}
                />
              </div>

              <div className="mb-3">
                <label className="form-label">
                  <i className="fas fa-credit-card me-2"></i> Card Number
                </label>
                <input
                  type="text"
                  name="card"
                  className="form-control"
                  required
                  value={formData.card}
                  onChange={handleChange}
                />
              </div>

              <button type="submit" className="btn btn-success w-100">
                Pay {total}$
              </button>
            </form>
          )}
        </>
      )}
    </section>
  );
}
