import React, { useEffect } from "react";
import { Link } from "react-router-dom";
// particles js
import "particles.js";

const products = [
  { name: "Smart Phones", img: "smart-phone.png", link: "smart-phones" },
  { name: "Audio Devices", img: "audio-device.png", link: "audio-devices" },
  { name: "Smart Watches", img: "smart-watch.png", link: "smart-watches" },
  { name: "Cameras", img: "camera.png", link: "cameras" },
  { name: "Laptops", img: "laptop.png", link: "laptops" },
  { name: "Setups", img: "setup.png", link: "setups" },
];

const upcomingProducts = [
  { name: "AirPods Pro 3", price: "249$", img: "Apple-AirPods-Pro-3.webp" },
  {
    name: "Apple Watch Ultra 3",
    price: "799$",
    img: "apple-watch-ultra-3.png",
  },
  { name: "Iphone 17 ProMax", price: "1,199$", img: "iphone-17-pro-max.png" },
  {
    name: "Asus Rog Scar18 2025",
    price: "2,599$",
    img: "asus-rog-scar-18-2025.png",
  },
];

const brandLogos = [
  "samsung-logo.png",
  "apple-logo.png",
  "Lenovo-Logo.png",
  "asus-logo.png",
  "Xiaomi_logo.png",
  "Huawei_logo.png",
  "HP_logo.png",
  "Dell_Logo.png",
  "canon-logo.png",
  "sony-logo.png",
  "nvidia-logo.png",
  "amd-logo.png",
  "intel-logo.png",
  "logitech-logo.png",
  "microsoft-logo.png",
  "razer-logo.png",
  "toshiba-logo.png",
  "lg-logo.png",
  "oppo-logo.png",
  "oneplus-logo.png",
];

export default function Home() {
  useEffect(() => {
    if (window.particlesJS) {
      window.particlesJS.load(
        "particles-js",
        `${process.env.PUBLIC_URL}/particles.json`,
        function () {
          console.log("particles.json loaded");
        }
      );
    }
  }, []);

  return (
    <div className="container-fluid home">
      {/* ================= Landing Section ================= */}
      <section className="my-3">
        <div className="row">
          {/* Landing Image */}
          <div className="col-md d-flex flex-column justify-content-center align-items-center">
            <img
              src={`${process.env.PUBLIC_URL}/assets/landing.png`}
              alt="laptops"
              className="w-100 landing-img"
            />
          </div>

          {/* Landing Text */}
          <div className="col-md d-flex flex-column justify-content-center align-items-center gap-2">
            <h1 className="landing-txt">
              Voltix <i className="fa-solid fa-bolt text-warning"></i>
            </h1>
            <h3 className="landing-txt">
              <i className="fa-solid fa-quote-left"></i> Join the{" "}
              <span className="text-warning">digital revolution</span>{" "}
              <i className="fa-solid fa-quote-right"></i>
            </h3>
            <Link
              to="/products"
              className="btn btn-warning fs-3 landing-btn shadow"
            >
              Explore More
            </Link>
          </div>
        </div>
      </section>

      {/* ================= Products Section ================= */}
      <section className="my-3">
        <div className="container">
          <div className="row justify-content-center g-3">
            {products.map((product, index) => (
              <div key={index} className="col-12 col-md-4 col-lg-2">
                <Link
                  to={`/${product.link}`}
                  className="product-card card text-center fw-bold border border-2 border-warning h-100 d-flex flex-column align-items-center justify-content-between p-2 text-decoration-none"
                >
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/${product.img}`}
                    alt={product.name}
                    className="card-img"
                  />
                  <p className="m-0 card-title">{product.name}</p>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* ================= Amazing Section ================= */}
      <section className="amazing-section position-relative overflow-hidden w-100 rounded my-3">
        {/* Background Image */}
        <img
          src={`${process.env.PUBLIC_URL}/assets/products-on-sale.jpg`}
          alt="Products on Sale"
          className="amazing-bg"
        />

        {/* Particles Overlay */}
        <div id="particles-js" className="amazing-particles"></div>

        {/* Text + Button */}
        <div className="amazing-txt d-flex flex-column justify-content-center align-items-center gap-3 text-center">
          <h2 className="text-white fs-1 fw-bold landing-txt">
            Amazing Deals <br /> Just for You!
          </h2>
          <Link
            to="/products"
            className="btn btn-outline-warning fs-5 landing-btn"
          >
            Go To Products Section <i className="fa-solid fa-arrow-right"></i>
          </Link>
        </div>
      </section>

      {/* ================= Upcoming Products ================= */}
      <section className="upcoming-products my-3">
        <h2 className="d-flex justify-content-between align-items-center w-100 border-bottom border-3 border-warning p-3">
          <span>Upcoming Products</span>
          <Link to="/products" className="text-warning text-decoration-none">
            Go To Products <i className="fa-solid fa-arrow-right"></i>
          </Link>
        </h2>

        <div className="row g-3 mt-3 justify-content-center">
          {upcomingProducts.map((item, idx) => (
            <div key={idx} className="col-12 col-sm-6 col-md-4 col-lg-3">
              <div className="card upcoming-card h-100 p-2 text-center">
                <img
                  src={`${process.env.PUBLIC_URL}/assets/${item.img}`}
                  alt={item.name}
                  className="product-img border-bottom border-warning border-1"
                />
                <p className="mt-2 fw-bold">{item.name}</p>
                <p className="fw-bold">
                  Price: <span className="text-warning">{item.price}</span>
                </p>
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* ================= Breaking News ================= */}
      <section className="breaking-news my-3">
        <h2 className="d-flex justify-content-between align-items-center border-bottom border-3 border-warning p-3">
          <span>Breaking News</span>
          <span className="text-warning">
            This Products Available Now{" "}
            <i className="fa-solid fa-thumbs-up"></i>
          </span>
        </h2>

        <div className="row g-3 mt-3">
          <div className="col-12 col-md-8">
            <img
              src={`${process.env.PUBLIC_URL}/assets/iPhone-series.png`}
              alt="iPhone Series"
              className="news-img w-100 rounded"
            />
          </div>
          <div className="col-12 col-md-4">
            <img
              src={`${process.env.PUBLIC_URL}/assets/play-station-5.png`}
              alt="Play Station 5"
              className="news-img w-100 rounded"
            />
          </div>
          <div className="col-12">
            <img
              src={`${process.env.PUBLIC_URL}/assets/smart watches.png`}
              alt="Smart Watches"
              className="news-img w-100 rounded"
            />
          </div>
        </div>
      </section>

      {/* ================= Brands Section ================= */}
      <section className="brands-section my-3 overflow-hidden">
        <h2 className="d-flex justify-content-between align-items-center border-bottom border-3 border-warning p-3">
          <span className="text-warning">Brands We Work With</span>
        </h2>

        <div className="brand-slider">
          <div className="brand-track d-flex align-items-center">
            {brandLogos.concat(brandLogos).map((logo, index) => (
              <div key={index} className="brand-slide-item mx-3">
                <img
                  src={`${process.env.PUBLIC_URL}/assets/${logo}`}
                  alt={logo.split("-")[0]}
                  className="brand-logo"
                />
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}
