import React from "react";

export default function ContactUs() {
  return (
    <div className="contact-container">
      <div className="contact-card">
        <h2 className="text-center mb-4"> Contact Us</h2>
        <p className="text-center text-muted mb-4">
          Have questions? Reach out to us directly!
        </p>

        <div className="contact-info mb-4">
          <p>
            <i className="fas fa-user me-2 text-warning"></i>
            <strong>Name:</strong> abo al magd
          </p>
          <p>
            <i className="fas fa-phone me-2 text-warning"></i>
            <strong>Phone:</strong> +2 010 9555 4022
          </p>
          <p>
            <i className="fab fa-whatsapp me-2 text-success"></i>
            <strong>WhatsApp:</strong> +2 010 9555 4022
          </p>
          <p>
            <i className="fas fa-envelope me-2 text-primary"></i>
            <strong>Email:</strong>{" "}
            <a href="mailto:abo.al.magd.404@gmail.com">
              abo.al.magd.404@gmail.com
            </a>
          </p>
        </div>

        <hr />

        <h5 className="mb-3">Send a Message</h5>
        <form>
          <div className="mb-3">
            <label className="form-label">Your Name</label>
            <input
              type="text"
              className="form-control"
              placeholder="Enter your name"
              required
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Your Email</label>
            <input
              type="email"
              className="form-control"
              placeholder="Enter your email"
              required
            />
          </div>
          <div className="mb-3">
            <label className="form-label">Message</label>
            <textarea
              className="form-control"
              rows="4"
              placeholder="Write your message..."
              required
            ></textarea>
          </div>
          <button type="submit" className="btn btn-warning w-100">
            <i className="fas fa-paper-plane me-2"></i> Send Message
          </button>
        </form>
      </div>
    </div>
  );
}
