import React, { useEffect, useState } from "react";

export default function Cameras() {
  const products = [
    {
      id: 1,
      title: "Apple MacBook Pro 16 M3 Max",
      image:
        "https://hw-egypt.com/wp-content/uploads/2024/05/MacBook-Pro-14-inch-M3-Pro-Chip-with-12-core-CPU-18-core-GPU-18GB-Unified-Memory-1TB-SSD-Storage-2.jpg",
      description: "16-inch Liquid Retina XDR, M3 Max chip, 32GB RAM",
      price: 3499,
    },
    {
      id: 2,
      title: "Dell XPS 15 OLED",
      image:
        "https://media.wired.com/photos/6169f03ba3578ac8067ae38c/191:100/w_1280,c_limit/Gear-Dell-XPS-15-OLED.jpg?mbid=social_retweet",
      description: "15.6-inch 3.5K OLED, Intel i7, RTX 4050",
      price: 2299,
    },
    {
      id: 3,
      title: "HP Spectre x360 14",
      image: "https://2b.com.eg/pub/media/catalog/product/z/h/zh060_1-3.jpg",
      description: "2-in-1 laptop, 13.5-inch OLED, Intel Evo i7",
      price: 1599,
    },
    {
      id: 4,
      title: "Lenovo ThinkPad X1 Carbon Gen 11",
      image:
        "https://p3-ofp.static.pub//fes/cms/2024/07/05/umcrxcnsm2br1itju6gvundeb9s6tf364734.png",
      description: "14-inch business laptop, Intel i7, lightweight",
      price: 1899,
    },
    {
      id: 5,
      title: "ASUS ROG Zephyrus G16",
      image:
        "https://egyptlaptop.com/images/detailed/72/Asus-Laptop-Gaming.webp",
      description: "Gaming laptop, Intel i9, RTX 4070, 240Hz QHD",
      price: 2499,
    },
    {
      id: 6,
      title: "MSI Raider GE78 HX",
      image:
        "https://egyptlaptop.com/images/detailed/70/Laptop-Msi-Raider.webp",
      description: "17-inch gaming laptop, i9, RTX 4090",
      price: 3499,
    },
    {
      id: 7,
      title: "Razer Blade 16",
      image:
        "https://assets2.razerzone.com/images/pnx.assets/62fd0244762cde2b9ccabf21add45c99/blade16-2025-1200x-630.webp",
      description: "16-inch QHD+ 240Hz, Intel i9, RTX 4080",
      price: 3299,
    },
    {
      id: 8,
      title: "Acer Swift X 14",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRLMTdi6yKmNmTCEJpcUmo0rGSaw_Fq57Zu7A&s",
      description: "14-inch OLED, Intel i7, RTX 4050",
      price: 1399,
    },
    {
      id: 9,
      title: "Microsoft Surface Laptop Studio 2",
      image: "https://m.media-amazon.com/images/I/61vqI0vw94L.jpg",
      description: "14.4-inch touch display, Intel i7, RTX 4060",
      price: 2099,
    },
    {
      id: 10,
      title: "Samsung Galaxy Book3 Ultra",
      image:
        "https://i.pcmag.com/imagery/reviews/02AvbMFp617QyK6xVyfay5z-3..v1680209588.jpg",
      description: "16-inch AMOLED, Intel i7, RTX 4050",
      price: 2199,
    },
    {
      id: 11,
      title: "Apple MacBook Air 15 M3",
      image:
        "https://m.media-amazon.com/images/I/71zbUS9KpQL._UF894,1000_QL80_.jpg",
      description: "15-inch Retina, M3 chip, fanless design",
      price: 1499,
    },
    {
      id: 12,
      title: "Dell Inspiron 16 Plus",
      image:
        "https://m.media-amazon.com/images/I/71+3Nxl3NtL._UF894,1000_QL80_.jpg",
      description: "16-inch 3K, Intel i7, RTX 3050",
      price: 1249,
    },
    {
      id: 13,
      title: "HP Omen 17",
      image:
        "https://pimcdn.sharafdg.com/cdn-cgi/image/width=600,height=600,fit=pad,format=webp,quality=70/images/000000000001002958_1?1703577251",
      description: "Gaming laptop, i9, RTX 4080, 240Hz",
      price: 2599,
    },
    {
      id: 14,
      title: "Lenovo Legion 7i Pro",
      image:
        "https://p1-ofp.static.pub//fes/cms/2025/07/04/r6bgnqqkqvwkdkv1dkvomxbsychdh1508265.png",
      description: "16-inch gaming laptop, Intel i9, RTX 4090",
      price: 2899,
    },
    {
      id: 15,
      title: "ASUS ZenBook Pro Duo 14",
      image:
        "https://m.media-amazon.com/images/I/71kb9bbY86L._UF894,1000_QL80_.jpg",
      description: "Dual-screen laptop, Intel i7, RTX 3060",
      price: 1999,
    },
    {
      id: 16,
      title: "MSI Prestige 16 Evo",
      image:
        "https://storage-asset.msi.com/global/picture/image/feature/nb/Prestige/Prestige-16-Evo-A13M/kv-laptop.png",
      description: "Lightweight ultrabook, Intel Evo platform",
      price: 1599,
    },
    {
      id: 17,
      title: "Razer Blade 14",
      image:
        "https://m.media-amazon.com/images/I/712g5R0vkbL._UF894,1000_QL80_.jpg",
      description: "14-inch gaming, AMD Ryzen 9, RTX 4070",
      price: 2199,
    },
    {
      id: 18,
      title: "Acer Predator Helios Neo 16",
      image:
        "https://sm.pcmag.com/pcmag_au/photo/default/048k1fwmbkreyv5x95yekez-2_2me2.jpg",
      description: "Gaming laptop, i7, RTX 4060",
      price: 1699,
    },
    {
      id: 19,
      title: "LG Gram 17",
      image: "https://m.media-amazon.com/images/I/81inFqt1MiL.jpg",
      description: "17-inch ultralight, Intel i7, long battery life",
      price: 1799,
    },
    {
      id: 20,
      title: "Framework Laptop 16",
      image:
        "https://media.cnn.com/api/v1/images/stellar/prod/framework-laptop-16-product-card-cnnu.jpg?c=16x9&q=h_720,w_1280,c_fill",
      description: "Modular design, Intel i7, repairable parts",
      price: 1999,
    },
  ];

  const [cart, setCart] = useState([]);
  const [quantities, setQuantities] = useState({});

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);

    const storedQuantities = storedCart.reduce((acc, item) => {
      acc[item.id] = item.quantity;
      return acc;
    }, {});
    setQuantities(storedQuantities);
  }, []);

  const updateLocalStorage = (updatedCart) =>
    localStorage.setItem("cart", JSON.stringify(updatedCart));

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => ({
      ...prev,
      [product.id]: (prev[product.id] || 0) + 1,
    }));
  };

  const handleRemoveFromCart = (product) => {
    let updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (!existing) return;

    if (existing.quantity > 1) {
      existing.quantity -= 1;
    } else {
      updatedCart = updatedCart.filter((item) => item.id !== product.id);
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => {
      const newQuantities = { ...prev };
      if (newQuantities[product.id] > 1) {
        newQuantities[product.id] -= 1;
      } else {
        delete newQuantities[product.id];
      }
      return newQuantities;
    });
  };

  return (
    <section className="container my-4 category-section">
      <h2 className="fw-bold header">Laptops</h2>

      <div className="row g-3">
        {products.map((p) => (
          <div key={p.id} className="col-12">
            <div className="card p-3 d-flex flex-row align-items-center smart-phone-card">
              {/* Details */}
              <div className="flex-grow-1 smart-phone-details">
                <h5>{p.title}</h5>
                <p className="text-muted">{p.description}</p>
                <p className="fw-bold">{p.price}$</p>

                <button
                  className="btn btn-warning position-relative me-2"
                  onClick={() => handleAddToCart(p)}
                >
                  Add
                  <span
                    className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${
                      quantities[p.id] ? "d-inline" : "d-none"
                    }`}
                  >
                    {quantities[p.id]}
                  </span>
                </button>

                <button
                  className="btn btn-outline-danger"
                  onClick={() => handleRemoveFromCart(p)}
                  disabled={!quantities[p.id]}
                >
                  Remove
                </button>
              </div>

              {/* Image */}
              <div className="smart-phone-image-wrapper ms-3">
                <img
                  src={p.image}
                  alt={p.title}
                  className="rounded smart-phone-image"
                  style={{
                    width: "200px",
                    height: "200px",
                    objectFit: "cover",
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Preview */}
      <div className="mt-5">
        <h3>Cart Items</h3>
        {cart.length === 0 ? (
          <p className="text-muted">Cart is empty</p>
        ) : (
          <ul className="list-group">
            {cart.map((item) => (
              <li
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {item.title} (x{item.quantity})
                <span className="fw-bold">{item.price * item.quantity}$</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
