import React, { useEffect, useState } from "react";

export default function AudioDevices() {
  const products = [
    {
      id: 1,
      title: "Sony WH-1000XM5",
      image: "https://m.media-amazon.com/images/I/51aXvjzcukL.jpg",
      description: "Noise-canceling over-ear headphones with 30h battery",
      price: 399,
    },
    {
      id: 2,
      title: "Bose QuietComfort 45",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRacsfu6gs-VbK6C3S_wRAapEGiyPieOnoicw&s",
      description: "Comfortable wireless headphones with noise cancellation",
      price: 349,
    },
    {
      id: 3,
      title: "Apple AirPods Max",
      image:
        "https://www.apple.com/v/airpods-max/f/images/meta/airpods-max_overview__c2mz40a3bugm_og.png",
      description: "Over-ear headphones with spatial audio and premium sound",
      price: 549,
    },
    {
      id: 4,
      title: "JBL Quantum 800",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSVEU2Ew4amDFKZXJoUVPIK6bsj42fQKbU-FA&s",
      description: "Wireless gaming headphones with immersive sound",
      price: 199,
    },
    {
      id: 5,
      title: "Sennheiser HD 560S",
      image:
        "https://thumbs.static-thomann.de/thumb/padthumb600x600/pics/bdb/_50/501912/15561955_800.jpg",
      description: "Open-back wired headphones for critical listening",
      price: 199,
    },
    {
      id: 6,
      title: "Beats Studio3",
      image:
        "https://m.media-amazon.com/images/I/61akJzywmPL._UF894,1000_QL80_.jpg",
      description: "Wireless noise-canceling headphones with Apple W1 chip",
      price: 349,
    },
    {
      id: 7,
      title: "Audio-Technica ATH-M50x",
      image:
        "https://www.audio-technica.com/media/catalog/product/a/t/ath-m50x_lab_01.png",
      description: "Professional studio monitor headphones",
      price: 149,
    },
    {
      id: 8,
      title: "Bose SoundLink Revolve+",
      image:
        "https://cairosales.com/18556-large_default/bose-soundlink-revolve-portable-bluetooth-360-speaker-12-hours-silver-soundlink-revolve-gry-240v-ap6.jpg",
      description: "Portable Bluetooth speaker with 360 sound",
      price: 299,
    },
    {
      id: 9,
      title: "Ultimate Ears Boom 3",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTyAWDIIe0cX0vUzQuULER8-V7f8MPhZ6rdBQ&s",
      description: "Waterproof portable Bluetooth speaker with deep bass",
      price: 149,
    },
    {
      id: 10,
      title: "Marshall Emberton",
      image: "https://m.media-amazon.com/images/I/71Sc5BDeJjL.jpg",
      description: "Compact portable Bluetooth speaker with rich sound",
      price: 149,
    },
    {
      id: 11,
      title: "Bang & Olufsen Beoplay H95",
      image:
        "https://m.media-amazon.com/images/I/61zanAzQ79L._UF894,1000_QL80_.jpg",
      description: "Luxury over-ear headphones with adaptive ANC",
      price: 849,
    },
    {
      id: 12,
      title: "Razer Kraken V3",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSFrQeD_9pIAfG5-kq37qbWUqS2fBODi_eGsA&s",
      description: "Gaming headphones with THX Spatial Audio",
      price: 129,
    },
    {
      id: 13,
      title: "Shure AONIC 50",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSgpcVPfAowaRqzYcyWQXnmjypyz5ec6YXjeQ&s",
      description: "Wireless noise-canceling headphones with hi-res audio",
      price: 399,
    },
    {
      id: 14,
      title: "Plantronics BackBeat Pro 2",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSc3BODmk5Q6Hd9mf-gBKc-vfDYBgAbZiP3Aw&s",
      description: "Bluetooth over-ear headphones with ANC",
      price: 249,
    },
    {
      id: 15,
      title: "Sony SRS-XB43",
      image:
        "https://www.sony.com/image/cd93e6697bbf6cd26e071cd27637bb31?fmt=pjpeg&wid=1400&bgcolor=F1F5F9&bgc=F1F5F9",
      description: "Portable wireless speaker with EXTRA BASS",
      price: 179,
    },
    {
      id: 16,
      title: "JBL Flip 6",
      image:
        "https://2b.com.eg/media/catalog/product/cache/661473ab953cdcdf4c3b607144109b90/s/p/sp107-29-9-2022.jpg",
      description: "Portable waterproof Bluetooth speaker",
      price: 129,
    },
    {
      id: 17,
      title: "Anker Soundcore Flare 2",
      image:
        "https://three-egypt.com/cdn/shop/files/AnkerSoundcoreFlare2BluetoothSpeakerThree-Egypt.com_1.webp?crop=center&height=1200&v=1725802366&width=1200",
      description: "360 surround Bluetooth speaker with BassUp",
      price: 99,
    },
    {
      id: 18,
      title: "Creative Sound BlasterX G6",
      image:
        "https://m.media-amazon.com/images/I/51q55xk4CYL.jpg_BO30,255,255,255_UF900,850_SR1910,1000,0,C_QL100_.jpg",
      description: "External USB DAC and amp for gaming audio",
      price: 149,
    },
    {
      id: 19,
      title: "FiiO K3",
      image:
        "https://www.audioconcierge.co.uk/wp-content/uploads/2023/12/FIIOK3ES.webp",
      description: "Compact DAC/amp for hi-res audio",
      price: 99,
    },
    {
      id: 20,
      title: "Cambridge Audio Melomania 1",
      image: "https://m.media-amazon.com/images/I/61IjMFoJAlL.jpg",
      description: "True wireless in-ear headphones",
      price: 129,
    },
  ];

  const [cart, setCart] = useState([]);
  const [quantities, setQuantities] = useState({});

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);

    const storedQuantities = storedCart.reduce((acc, item) => {
      acc[item.id] = item.quantity;
      return acc;
    }, {});
    setQuantities(storedQuantities);
  }, []);

  const updateLocalStorage = (updatedCart) =>
    localStorage.setItem("cart", JSON.stringify(updatedCart));

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => ({
      ...prev,
      [product.id]: (prev[product.id] || 0) + 1,
    }));
  };

  const handleRemoveFromCart = (product) => {
    let updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (!existing) return;

    if (existing.quantity > 1) {
      existing.quantity -= 1;
    } else {
      updatedCart = updatedCart.filter((item) => item.id !== product.id);
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => {
      const newQuantities = { ...prev };
      if (newQuantities[product.id] > 1) {
        newQuantities[product.id] -= 1;
      } else {
        delete newQuantities[product.id];
      }
      return newQuantities;
    });
  };

  return (
    <section className="container my-4 category-section">
      <h2 className="fw-bold header">Audio Devices</h2>

      <div className="row g-3">
        {products.map((p) => (
          <div key={p.id} className="col-12">
            <div className="card p-3 d-flex flex-row align-items-center smart-phone-card">
              {/* Details */}
              <div className="flex-grow-1 smart-phone-details">
                <h5>{p.title}</h5>
                <p className="text-muted">{p.description}</p>
                <p className="fw-bold">{p.price}$</p>

                <button
                  className="btn btn-warning position-relative me-2"
                  onClick={() => handleAddToCart(p)}
                >
                  Add
                  <span
                    className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${
                      quantities[p.id] ? "d-inline" : "d-none"
                    }`}
                  >
                    {quantities[p.id]}
                  </span>
                </button>

                <button
                  className="btn btn-outline-danger"
                  onClick={() => handleRemoveFromCart(p)}
                  disabled={!quantities[p.id]}
                >
                  Remove
                </button>
              </div>

              {/* Image */}
              <div className="smart-phone-image-wrapper ms-3">
                <img
                  src={p.image}
                  alt={p.title}
                  className="rounded smart-phone-image"
                  style={{
                    width: "200px",
                    height: "200px",
                    objectFit: "cover",
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Preview */}
      <div className="mt-5">
        <h3>Cart Items</h3>
        {cart.length === 0 ? (
          <p className="text-muted">Cart is empty</p>
        ) : (
          <ul className="list-group">
            {cart.map((item) => (
              <li
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {item.title} (x{item.quantity})
                <span className="fw-bold">{item.price * item.quantity}$</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
