import React from "react";
import { Link } from "react-router-dom";

export default function Setups() {
  return (
    <section className="container d-flex flex-column align-items-center justify-content-center text-center py-5 h-75">
      {/* Title */}
      <h1 className="fw-bold display-4 text-warning mb-4">Coming Soon</h1>

      {/* Icons */}
      <div className="d-flex gap-4 mb-4 fs-1 text-secondary">
        <i className="fa-solid fa-computer"></i>
        <i className="fa-solid fa-keyboard"></i>
        <i className="fa-solid fa-mouse"></i>
        <i className="fa-solid fa-display"></i>
        <i className="fa-solid fa-headset"></i>
      </div>

      {/* Message */}
      <p className="lead text-muted mb-5">
        We are preparing amazing setups for productivity and gaming. Stay tuned!
      </p>

      {/* Back Button */}
      <Link to="/" className="btn btn-outline-warning">
        <i className="fa-solid fa-arrow-left me-2"></i> Back to Home
      </Link>
    </section>
  );
}
