import React, { useEffect, useState } from "react";

export default function Cameras() {
  const products = [
    {
      id: 1,
      title: "Canon EOS R5",
      image:
        "https://cdn.media.amplience.net/i/canon/eos-r5_front_rf24-105mmf4lisusm_square_32c26ad194234d42b3cd9e582a21c99b",
      description: "45MP full-frame mirrorless, 8K video",
      price: 3899,
    },
    {
      id: 2,
      title: "Sony A7 IV",
      image:
        "https://camerashop.com.eg/wp-content/uploads/2022/04/Alpha-7-IV-Full-frame-Interchangeable-Lens-Camera-33MP-10FPS-4K-60p-Front-.webp",
      description: "33MP full-frame mirrorless, 4K 60p video",
      price: 2799,
    },
    {
      id: 3,
      title: "Nikon Z9",
      image:
        "https://www.nikon-mea.com/media/catalog/product/n/i/nikon-z-9-product-image-front-grey-2.jpeg?optimize=high&fit=bounds&height=690&width=690&canvas=690:690",
      description: "45.7MP stacked CMOS, 8K video, pro body",
      price: 5499,
    },
    {
      id: 4,
      title: "Fujifilm X-T5",
      image:
        "https://general.eg/wp-content/uploads/2023/08/Fujifilm-X-T5-body-silver-2.jpg",
      description: "40MP APS-C mirrorless, 6.2K video",
      price: 1699,
    },
    {
      id: 5,
      title: "Panasonic Lumix GH6",
      image: "https://www.castlecameras.co.uk/images/products/large/14394.jpg",
      description: "25MP MFT sensor, 5.7K ProRes video",
      price: 2199,
    },
    {
      id: 6,
      title: "Olympus OM-1",
      image:
        "https://www.olympus-global.com/technology/museum/camera/products/images/om-1.jpg",
      description: "20MP MFT stacked sensor, weather-sealed",
      price: 1999,
    },
    {
      id: 7,
      title: "Leica Q2",
      image:
        "https://leicastoremiami.com/cdn/shop/products/Q2_Monochrom_FRONT_RGB.jpg?v=1604858481",
      description: "47MP full-frame compact, Summilux 28mm lens",
      price: 4995,
    },
    {
      id: 8,
      title: "GoPro HERO12 Black",
      image: "https://m.media-amazon.com/images/I/71p5V8+OnfL.jpg",
      description: "5.3K60 video, waterproof, HyperSmooth",
      price: 399,
    },
    {
      id: 9,
      title: "Insta360 X3",
      image:
        "https://general.eg/wp-content/uploads/2024/03/Insta360-X3-360%C2%B0-Camera-7.jpg",
      description: "360 action camera, 5.7K HDR video",
      price: 449,
    },
    {
      id: 10,
      title: "DJI Pocket 2",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQWEFFjTVsIj8pOE3p1XakkAPM7CPlQ7uRrFA&s",
      description: "Pocket gimbal camera, 4K video",
      price: 349,
    },
    {
      id: 11,
      title: "Canon EOS R6 Mark II",
      image:
        "https://camerashop.com.eg/wp-content/uploads/2023/07/Canon-EOS-R6-Mark-II-Mirrorless-Camera-Front-.webp",
      description: "24MP full-frame, 4K 60p video",
      price: 2499,
    },
    {
      id: 12,
      title: "Sony ZV-E10",
      image:
        "https://sony.scene7.com/is/image/sonyglobalsolutions/Primary_image?$categorypdpnav$&fmt=png-alpha",
      description: "APS-C mirrorless, vlogging-friendly",
      price: 699,
    },
    {
      id: 13,
      title: "Nikon Z fc",
      image:
        "https://camerashop.com.eg/wp-content/uploads/2022/04/Nikon-Z-fc-Mirrorless-Digital-Camera-Body-Only-Front-.webp",
      description: "Retro design, 20.9MP APS-C mirrorless",
      price: 959,
    },
    {
      id: 14,
      title: "Fujifilm GFX100S",
      image: "https://m.media-amazon.com/images/I/81+Ij7DrZWL.jpg",
      description: "102MP medium format, 4K video",
      price: 5999,
    },
    {
      id: 15,
      title: "Panasonic Lumix S5 II",
      image:
        "https://m.media-amazon.com/images/I/81oBmjgFxPL._UF894,1000_QL80_.jpg",
      description: "24MP full-frame, phase detect AF, 6K video",
      price: 1999,
    },
    {
      id: 16,
      title: "Ricoh GR IIIx",
      image:
        "https://shuttershopegypt.com/wp-content/uploads/2024/08/1633538894_1664769.jpg",
      description: "24MP APS-C compact, 40mm equiv lens",
      price: 999,
    },
    {
      id: 17,
      title: "Leica SL2",
      image:
        "https://m.media-amazon.com/images/I/71PSfnDw+IL._UF894,1000_QL80_.jpg",
      description: "47MP full-frame mirrorless, Leica build",
      price: 6295,
    },
    {
      id: 18,
      title: "GoPro Max",
      image: "https://m.media-amazon.com/images/I/51ElbW93BGL.jpg",
      description: "360 action camera, 6K spherical video",
      price: 499,
    },
    {
      id: 19,
      title: "Insta360 ONE RS Twin Edition",
      image:
        "https://shuttershopegypt.com/wp-content/uploads/2024/08/1647945062_IMG_1717522.jpg",
      description: "4K Boost + 360 mod interchangeable lenses",
      price: 549,
    },
    {
      id: 20,
      title: "DJI Osmo Action 4",
      image:
        "https://camerashop.com.eg/wp-content/uploads/2024/10/DJI-Osmo-Action-4-Camera.jpg",
      description: "Action camera, 4K HDR, waterproof",
      price: 399,
    },
  ];

  const [cart, setCart] = useState([]);
  const [quantities, setQuantities] = useState({});

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);

    const storedQuantities = storedCart.reduce((acc, item) => {
      acc[item.id] = item.quantity;
      return acc;
    }, {});
    setQuantities(storedQuantities);
  }, []);

  const updateLocalStorage = (updatedCart) =>
    localStorage.setItem("cart", JSON.stringify(updatedCart));

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => ({
      ...prev,
      [product.id]: (prev[product.id] || 0) + 1,
    }));
  };

  const handleRemoveFromCart = (product) => {
    let updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (!existing) return;

    if (existing.quantity > 1) {
      existing.quantity -= 1;
    } else {
      updatedCart = updatedCart.filter((item) => item.id !== product.id);
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => {
      const newQuantities = { ...prev };
      if (newQuantities[product.id] > 1) {
        newQuantities[product.id] -= 1;
      } else {
        delete newQuantities[product.id];
      }
      return newQuantities;
    });
  };

  return (
    <section className="container my-4 category-section">
      <h2 className="fw-bold header">Cameras</h2>

      <div className="row g-3">
        {products.map((p) => (
          <div key={p.id} className="col-12">
            <div className="card p-3 d-flex flex-row align-items-center smart-phone-card">
              {/* Details */}
              <div className="flex-grow-1 smart-phone-details">
                <h5>{p.title}</h5>
                <p className="text-muted">{p.description}</p>
                <p className="fw-bold">{p.price}$</p>

                <button
                  className="btn btn-warning position-relative me-2"
                  onClick={() => handleAddToCart(p)}
                >
                  Add
                  <span
                    className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${
                      quantities[p.id] ? "d-inline" : "d-none"
                    }`}
                  >
                    {quantities[p.id]}
                  </span>
                </button>

                <button
                  className="btn btn-outline-danger"
                  onClick={() => handleRemoveFromCart(p)}
                  disabled={!quantities[p.id]}
                >
                  Remove
                </button>
              </div>

              {/* Image */}
              <div className="smart-phone-image-wrapper ms-3">
                <img
                  src={p.image}
                  alt={p.title}
                  className="rounded smart-phone-image"
                  style={{
                    width: "200px",
                    height: "200px",
                    objectFit: "cover",
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Preview */}
      <div className="mt-5">
        <h3>Cart Items</h3>
        {cart.length === 0 ? (
          <p className="text-muted">Cart is empty</p>
        ) : (
          <ul className="list-group">
            {cart.map((item) => (
              <li
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {item.title} (x{item.quantity})
                <span className="fw-bold">{item.price * item.quantity}$</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
