import React, { useEffect, useState } from "react";

export default function SmartWatches() {
  const products = [
    {
      id: 1,
      title: "Apple Watch Series 9",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR8MQ61_mmtc_REVOfQ3a6L5hBgr9oP72e9wA&s",
      description: "Always-On Retina display, ECG, 32GB storage",
      price: 399,
    },
    {
      id: 2,
      title: "Samsung Galaxy Watch 6",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSgN9elDR909EoJSpbUbwiHUQubF2NhoBDCTA&s",
      description: "1.4-inch AMOLED, Blood Pressure, GPS",
      price: 349,
    },
    {
      id: 3,
      title: "Garmin Fenix 7",
      image:
        "https://i5.walmartimages.com/asr/9b0d5960-b590-459a-8d6a-f25d422c4adf.4f99c4b1fd2d8035ad6f339f4c228fff.jpeg",
      description: "Multisport GPS smartwatch, solar charging, 18 days battery",
      price: 699,
    },
    {
      id: 4,
      title: "Fitbit Sense 2",
      image:
        "https://m.media-amazon.com/images/I/51GeUyZUB9L._UF894,1000_QL80_.jpg",
      description: "Stress management, heart rate monitoring, sleep tracking",
      price: 299,
    },
    {
      id: 5,
      title: "Apple Watch SE 2",
      image: "https://m.media-amazon.com/images/I/71LfnkRgZ4L.jpg",
      description: "Retina display, Fitness tracking, 32GB",
      price: 249,
    },
    {
      id: 6,
      title: "Samsung Galaxy Watch 5",
      image:
        "https://images.samsung.com/is/image/samsung/p6pim/eg/2208/gallery/eg-galaxy-watch5-44mm-sm-r910nzaamea-533192755?$684_547_PNG$",
      description: "AMOLED display, heart rate sensor, GPS",
      price: 299,
    },
    {
      id: 7,
      title: "Garmin Venu 2",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTRDXsHer5kUNQ_oYxsNFSI85Xc-V9iuoVKNA&s",
      description: "AMOLED display, health tracking, 11 days battery",
      price: 399,
    },
    {
      id: 8,
      title: "Fossil Gen 6",
      image:
        "https://m.media-amazon.com/images/I/71DP0v5VqhL._UF894,1000_QL80_.jpg",
      description: "Wear OS, heart rate, Google Assistant",
      price: 299,
    },
    {
      id: 9,
      title: "Huawei Watch GT 3",
      image: "https://m.media-amazon.com/images/I/61aiRppXG+L.jpg",
      description: "1.43-inch AMOLED, GPS, 14 days battery",
      price: 229,
    },
    {
      id: 10,
      title: "Amazfit GTR 4",
      image:
        "https://ucarecdn.com/70443f42-aa0c-4294-9d05-df6f5e923f27/-/format/auto/-/preview/3000x3000/-/quality/lighter/%E7%BB%84%20132@2x.jpg",
      description: "AMOLED display, 90 sports modes, 14 days battery",
      price: 179,
    },
    {
      id: 11,
      title: "Apple Watch Ultra",
      image:
        "https://smartkoshk.com/cdn/shop/files/98483ED9-4FC3-43DE-B63F-48B73AADD3E8.webp?v=1697415497&width=1946",
      description: "Titanium case, 49mm, extreme sports tracking",
      price: 799,
    },
    {
      id: 12,
      title: "Suunto 9 Peak",
      image:
        "https://www.suunto.com/globalassets/productimages/suunto-9-peak/all-black/front/ss050522000-suunto-9-peak-all-black-front-view-watchface-blue-altitude-01.png?height=900&format=jpg",
      description: "GPS smartwatch, 170h battery, 80 sport modes",
      price: 499,
    },
    {
      id: 13,
      title: "Garmin Forerunner 955",
      image: "https://m.media-amazon.com/images/I/71yandsiOKL.jpg",
      description: "Running smartwatch, GPS, heart rate monitor",
      price: 499,
    },
    {
      id: 14,
      title: "TicWatch Pro 3",
      image:
        "https://m.media-amazon.com/images/I/71INtfU-f-L._UF1000,1000_QL80_.jpg",
      description: "Wear OS, AMOLED display, GPS, heart rate",
      price: 299,
    },
    {
      id: 15,
      title: "Withings ScanWatch",
      image:
        "https://m.media-amazon.com/images/I/61dO8W6+E3L._UF894,1000_QL80_.jpg",
      description: "Hybrid smartwatch, ECG, SpO2, 30 days battery",
      price: 249,
    },
    {
      id: 16,
      title: "Polar Vantage V2",
      image:
        "https://www.polar.com/img/cms/0f5713394b8b221ab83203dcfd8fc3ca686100ae-1000x1000-1000.webp",
      description: "Multisport GPS watch, performance metrics",
      price: 499,
    },
    {
      id: 17,
      title: "Garmin Epix Gen 2",
      image:
        "https://m.media-amazon.com/images/I/71o1sLE513L._UF894,1000_QL80_.jpg",
      description: "AMOLED display, GPS, 16 days battery",
      price: 899,
    },
    {
      id: 18,
      title: "Mobvoi TicWatch GTH",
      image:
        "https://lcdpartner.com/25851-large_default/mobvoi-ticwatch-gth-2-black.jpg",
      description: "Health monitoring, 1.55-inch display, 10 days battery",
      price: 149,
    },
    {
      id: 19,
      title: "Fitbit Versa 4",
      image: "https://m.media-amazon.com/images/I/61OEuoqFqYL.jpg",
      description: "Fitness smartwatch, heart rate, sleep tracking",
      price: 199,
    },
    {
      id: 20,
      title: "Amazfit Bip U Pro",
      image:
        "https://www.ryans.com/storage/products/main/mi-amazfit-bip-u-pro-pink-smart-11666000122.webp",
      description: "Lightweight fitness watch, SpO2, GPS",
      price: 99,
    },
  ];

  const [cart, setCart] = useState([]);
  const [quantities, setQuantities] = useState({});

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);

    const storedQuantities = storedCart.reduce((acc, item) => {
      acc[item.id] = item.quantity;
      return acc;
    }, {});
    setQuantities(storedQuantities);
  }, []);

  const updateLocalStorage = (updatedCart) =>
    localStorage.setItem("cart", JSON.stringify(updatedCart));

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => ({
      ...prev,
      [product.id]: (prev[product.id] || 0) + 1,
    }));
  };

  const handleRemoveFromCart = (product) => {
    let updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (!existing) return;

    if (existing.quantity > 1) {
      existing.quantity -= 1;
    } else {
      updatedCart = updatedCart.filter((item) => item.id !== product.id);
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => {
      const newQuantities = { ...prev };
      if (newQuantities[product.id] > 1) {
        newQuantities[product.id] -= 1;
      } else {
        delete newQuantities[product.id];
      }
      return newQuantities;
    });
  };

  return (
    <section className="container my-4 category-section">
      <h2 className="fw-bold header">Smart Watches</h2>

      <div className="row g-3">
        {products.map((p) => (
          <div key={p.id} className="col-12">
            <div className="card p-3 d-flex flex-row align-items-center smart-phone-card">
              {/* Details */}
              <div className="flex-grow-1 smart-phone-details">
                <h5>{p.title}</h5>
                <p className="text-muted">{p.description}</p>
                <p className="fw-bold">{p.price}$</p>

                <button
                  className="btn btn-warning position-relative me-2"
                  onClick={() => handleAddToCart(p)}
                >
                  Add
                  <span
                    className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${
                      quantities[p.id] ? "d-inline" : "d-none"
                    }`}
                  >
                    {quantities[p.id]}
                  </span>
                </button>

                <button
                  className="btn btn-outline-danger"
                  onClick={() => handleRemoveFromCart(p)}
                  disabled={!quantities[p.id]}
                >
                  Remove
                </button>
              </div>

              {/* Image */}
              <div className="smart-phone-image-wrapper ms-3">
                <img
                  src={p.image}
                  alt={p.title}
                  className="rounded smart-phone-image"
                  style={{
                    width: "200px",
                    height: "200px",
                    objectFit: "cover",
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Preview */}
      <div className="mt-5">
        <h3>Cart Items</h3>
        {cart.length === 0 ? (
          <p className="text-muted">Cart is empty</p>
        ) : (
          <ul className="list-group">
            {cart.map((item) => (
              <li
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {item.title} (x{item.quantity})
                <span className="fw-bold">{item.price * item.quantity}$</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
