import React from "react";
import { Link } from "react-router-dom";

export default function ProductCards() {
  const products = [
    {
      title: "Smart Phones",
      description: "Explore the latest smartphones with cutting-edge features.",
      image:
        "https://images.unsplash.com/photo-1511707171634-5f897ff02aa9?auto=format&fit=crop&w=500&q=80",
      path: "/smart-phones",
    },
    {
      title: "Audio Devices",
      description: "High-quality headphones and speakers for every need.",
      image:
        "https://images.unsplash.com/photo-1583394838336-acd977736f90?auto=format&fit=crop&w=500&q=80",
      path: "/audio-devices",
    },
    {
      title: "Smart Watches",
      description: "Track your health and stay connected on the go.",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRQmpdFtX7XSFo3OouaODlEtPOO-5V5zMUJkg&s",
      path: "/smart-watches",
    },
    {
      title: "Cameras",
      description: "Capture your moments with professional cameras.",
      image:
        "https://i5.walmartimages.com/seo/Canon-EOS-5D-Mark-IV-DSLR-Camera-with-24-105mm-F-4L-II-Lens_761467b2-8369-4e43-bf35-7081f5fd4b87.5eb0e5b2f2866d05f02de5638f409d7b.jpeg",
      path: "/cameras",
    },
    {
      title: "Laptops",
      description: "Powerful laptops for work, gaming, and creativity.",
      image:
        "https://images.unsplash.com/photo-1517336714731-489689fd1ca8?auto=format&fit=crop&w=500&q=80",
      path: "/laptops",
    },
    {
      title: "Setups",
      description: "Complete computer setups for productivity and gaming.",
      image:
        "https://cdn.autonomous.ai/production/ecm/250721/living-room-gaming-setup00001.webp",
      path: "/setups",
    },
  ];

  return (
    <section className="products-section container mb-4">
      <h2 className="products-title text-center mb-4 fw-bold">Our Products</h2>

      <div className="row g-4">
        {products.map((item, index) => (
          <div key={index} className="col-12 col-sm-6 col-lg-4">
            <div
              className="product-card"
              style={{ backgroundImage: `url(${item.image})` }}
            >
              {/* overlay */}
              <div className="overlay"></div>

              {/* content */}
              <div className="card-body text-center">
                <h4 className="fw-bold product-title">{item.title}</h4>
                <p className="mb-2 product-description">{item.description}</p>

                <Link to={item.path} className="btn product-btn mt-3">
                  Continue Browsing <i className="fa-solid fa-arrow-right"></i>
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
