import React, { useEffect, useState } from "react";

export default function SmartPhones() {
  const products = [
    {
      id: 1,
      title: "iPhone 17 Pro",
      image:
        "https://media.egyin.com/2025/7/large/1769111444230120250714072404244.jpg",
      description: "6.7-inch OLED, A19 Pro chip, 256GB",
      price: 1199,
    },
    {
      id: 2,
      title: "Samsung Galaxy S23 Ultra",
      image:
        "https://cdn.shortpixel.ai/spai/q_glossy+ret_img+to_webp/mobizil.com/wp-content/uploads/2023/01/Samsung-Galaxy-S23-Ultra-5G.jpg",
      description: "6.8-inch AMOLED, Snapdragon 8 Gen 2, 256GB",
      price: 1199,
    },
    {
      id: 3,
      title: "Google Pixel 7 Pro",
      image: "https://m.media-amazon.com/images/I/615rI0PoyOL.jpg",
      description: "6.7-inch OLED, Tensor G2, 256GB",
      price: 899,
    },
    {
      id: 4,
      title: "OnePlus 11 Pro",
      image:
        "https://s.yimg.com/ny/api/res/1.2/fjSPvD5mvDw1YM2mZp76fg--/YXBwaWQ9aGlnaGxhbmRlcjt3PTI0MDA7aD0xNjAw/https://s.yimg.com/os/creatr-uploaded-images/2023-02/51e159f0-a624-11ed-bbfd-4caa789dbbc5",
      description: "6.7-inch AMOLED, Snapdragon 8 Gen 2, 512GB",
      price: 899,
    },
    {
      id: 5,
      title: "Xiaomi Mi 13 Pro",
      image:
        "https://mobizil.com/wp-content/uploads/2023/01/Xiaomi-13-Pro-1.jpg",
      description: "6.73-inch AMOLED, Snapdragon 8 Gen 2, 256GB",
      price: 849,
    },
    {
      id: 6,
      title: "Huawei P60 Pro",
      image:
        "https://mobizil.com/wp-content/uploads/2023/05/Huawei-P60-Pro-1.jpg",
      description: "6.6-inch OLED, Snapdragon 8+ Gen 1, 256GB",
      price: 999,
    },
    {
      id: 7,
      title: "Sony Xperia 1 V",
      image:
        "https://www.cnet.com/a/img/resize/3fa88ef4c8185e65049232953b36ac46369cdf91/hub/2023/05/10/c293a2d1-312e-4061-b001-9c389ce5caa9/p1003516-1.jpg?auto=webp&fit=crop&height=900&width=1200",
      description: "6.5-inch OLED, Snapdragon 8 Gen 2, 12MP triple camera",
      price: 1299,
    },
    {
      id: 8,
      title: "Oppo Find X6 Pro",
      image:
        "https://mobizil.com/wp-content/uploads/2023/05/Oppo-Find-X6-Pro.jpg",
      description:
        "6.7-inch AMOLED, Snapdragon 8 Gen 2, 50MP Hasselblad camera",
      price: 1199,
    },
    {
      id: 9,
      title: "Realme GT 3 Pro",
      image:
        "https://www.notebookcheck.net/fileadmin/_processed_/2/f/csm_Realme_GT_Neo_6_Press_Render_1_6_ebfbc1738f.jpg",
      description: "6.7-inch AMOLED, Snapdragon 8 Gen 2, 50MP triple camera",
      price: 999,
    },
    {
      id: 10,
      title: "Asus Zenfone 10",
      image:
        "https://dlcdnwebimgs.asus.com/gain/fb3d511e-8180-48e9-aa20-52e1f9dc607d/",
      description: "5.9-inch AMOLED, Snapdragon 8 Gen 2, 50MP dual camera",
      price: 849,
    },
    {
      id: 11,
      title: "Motorola Edge 40",
      image: "https://m.media-amazon.com/images/I/51fWXauEHnL.jpg",
      description: "6.55-inch OLED, Snapdragon 8 Gen 2, 256GB",
      price: 699,
    },
    {
      id: 12,
      title: "Nokia X50",
      image:
        "https://media.mobile.news/ArticleUpload/2025/7/21/IMG_5373_1674_090426.jpg",
      description: "6.5-inch OLED, Snapdragon 8 Gen 1, 128GB",
      price: 599,
    },
    {
      id: 13,
      title: "Vivo X90 Pro",
      image:
        "https://img4.dhresource.com/webp/m/0x0/f3/albu/ry/s/30/9cec361f-a5bc-480a-99e6-1298d5918aef.jpg",
      description: "6.78-inch AMOLED, MediaTek Dimensity 9200+, 256GB",
      price: 899,
    },
    {
      id: 14,
      title: "Lenovo Legion Y90",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSbqnWs3p2SGYriwvlp2GYENVkWbx5auTslqQ&s",
      description: "6.92-inch AMOLED, Snapdragon 8 Gen 2, 512GB",
      price: 1199,
    },
    {
      id: 15,
      title: "ZTE Axon 40 Ultra",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRsAnA7NcA5DunEJ9mQhJDvwXy3RmEsnVZMnQ&s",
      description: "6.8-inch AMOLED, Snapdragon 8 Gen 1, 256GB",
      price: 799,
    },
    {
      id: 16,
      title: "Honor Magic 6 Pro",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTe9V55JXuU6dYKjz4HwqnfRBS7Qp9Fic98dQ&s",
      description: "6.81-inch AMOLED, Snapdragon 8 Gen 2, 256GB",
      price: 899,
    },
    {
      id: 17,
      title: "Asus ROG Phone 7",
      image:
        "https://fdn.gsmarena.com/imgroot/reviews/23/asus-rog-phone-7/lifestyle/-1200w5/gsmarena_009.jpg",
      description: "6.78-inch AMOLED, Snapdragon 8 Gen 2, 512GB",
      price: 1099,
    },
    {
      id: 18,
      title: "Black Shark 6 Pro",
      image:
        "https://cms-assets.bajajfinserv.in/is/image/bajajfinance/black-shark-6-pro-price-in-india-DPF-ADP?scl=1&fmt=png-alpha",
      description: "6.67-inch AMOLED, Snapdragon 8 Gen 2, 256GB",
      price: 899,
    },
    {
      id: 19,
      title: "Meizu 20 Pro",
      image:
        "https://ae01.alicdn.com/kf/Sb6a6eb6d07c940be815f1e3aa1469cfcq.jpg_640x640q90.jpg",
      description: "6.8-inch AMOLED, Snapdragon 8 Gen 2, 256GB",
      price: 849,
    },
    {
      id: 20,
      title: "Realme Narzo 60 Pro",
      image: "https://m.media-amazon.com/images/I/71ABgrl9CXL.jpg",
      description: "6.6-inch AMOLED, MediaTek Dimensity 6100+, 128GB",
      price: 399,
    },
  ];

  const [cart, setCart] = useState([]);
  const [quantities, setQuantities] = useState({});

  useEffect(() => {
    const storedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(storedCart);

    const storedQuantities = storedCart.reduce((acc, item) => {
      acc[item.id] = item.quantity;
      return acc;
    }, {});
    setQuantities(storedQuantities);
  }, []);

  const updateLocalStorage = (updatedCart) =>
    localStorage.setItem("cart", JSON.stringify(updatedCart));

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (existing) {
      existing.quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => ({
      ...prev,
      [product.id]: (prev[product.id] || 0) + 1,
    }));
  };

  const handleRemoveFromCart = (product) => {
    let updatedCart = [...cart];
    const existing = updatedCart.find((item) => item.id === product.id);

    if (!existing) return;

    if (existing.quantity > 1) {
      existing.quantity -= 1;
    } else {
      updatedCart = updatedCart.filter((item) => item.id !== product.id);
    }

    setCart(updatedCart);
    updateLocalStorage(updatedCart);

    setQuantities((prev) => {
      const newQuantities = { ...prev };
      if (newQuantities[product.id] > 1) {
        newQuantities[product.id] -= 1;
      } else {
        delete newQuantities[product.id];
      }
      return newQuantities;
    });
  };

  return (
    <section className="container my-4 category-section">
      <h2 className="fw-bold header">Smart Phones</h2>

      <div className="row g-3">
        {products.map((p) => (
          <div key={p.id} className="col-12">
            <div className="card p-3 d-flex flex-row align-items-center smart-phone-card">
              {/* Details */}
              <div className="flex-grow-1 smart-phone-details">
                <h5>{p.title}</h5>
                <p className="text-muted">{p.description}</p>
                <p className="fw-bold">{p.price}$</p>

                <button
                  className="btn btn-warning position-relative me-2"
                  onClick={() => handleAddToCart(p)}
                >
                  Add
                  <span
                    className={`position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger ${
                      quantities[p.id] ? "d-inline" : "d-none"
                    }`}
                  >
                    {quantities[p.id]}
                  </span>
                </button>

                <button
                  className="btn btn-outline-danger"
                  onClick={() => handleRemoveFromCart(p)}
                  disabled={!quantities[p.id]}
                >
                  Remove
                </button>
              </div>

              {/* Image */}
              <div className="smart-phone-image-wrapper ms-3">
                <img
                  src={p.image}
                  alt={p.title}
                  className="rounded smart-phone-image"
                  style={{
                    width: "200px",
                    height: "200px",
                    objectFit: "cover",
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Preview */}
      <div className="mt-5">
        <h3>Cart Items</h3>
        {cart.length === 0 ? (
          <p className="text-muted">Cart is empty</p>
        ) : (
          <ul className="list-group">
            {cart.map((item) => (
              <li
                key={item.id}
                className="list-group-item d-flex justify-content-between align-items-center"
              >
                {item.title} (x{item.quantity})
                <span className="fw-bold">{item.price * item.quantity}$</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
