import React from "react";
import { Link } from "react-router-dom";

export default function NotFound() {
  return (
    <section className="notfound-section d-flex flex-column justify-content-center align-items-center text-center vh-100">
      <div className="icon-wrapper mb-3">
        <i className="fa-solid fa-triangle-exclamation"></i>
      </div>

      <h1 className="error-code">404</h1>
      <h3 className="error-title">Oops! Page Not Found</h3>
      <p className="error-text">
        The page you are looking for does not exist or was moved.
      </p>

      <Link to="/home" className="btn-home">
        <i className="fa-solid fa-house me-2"></i> Back to Home
      </Link>
    </section>
  );
}
