import { HashRouter, Navigate, Route, Routes } from "react-router-dom";
import "./App.css";
import { useState, useEffect } from "react";

import Navbar from "./components/Navbar";
import Home from "./components/Home";
import NotFound from "./components/NotFound";
import Footer from "./components/Footer";
import Products from "./components/Products";
import SmartPhones from "./components/SmartPhones";
import AudioDevices from "./components/AudioDevices";
import SmartWatches from "./components/SmartWatches";
import Cameras from "./components/Cameras";
import Laptops from "./components/Laptops";
import Setups from "./components/Setups";
import Cart from "./components/Cart";
import Auth from "./components/Auth";
import Account from "./components/Account";
import ContactUs from "./components/ContactUs";

function App() {
  const [user, setUser] = useState(null);

  useEffect(() => {
    const currentUser = JSON.parse(localStorage.getItem("currentUser"));
    if (currentUser) setUser(currentUser);
  }, []);

  const ProtectedRoute = ({ children }) => {
    if (!user) return <Navigate to="/auth" />;
    return children;
  };

  return (
    <div className="App app-container">
      <HashRouter>
        <Navbar user={user} setUser={setUser} />
        <div className="main-content">
          <Routes>
            <Route path="/" element={<Navigate to="/home" />} />
            <Route path="/home" element={<Home />} />
            <Route path="/products" element={<Products />} />
            <Route path="/smart-phones" element={<SmartPhones />} />
            <Route path="/audio-devices" element={<AudioDevices />} />
            <Route path="/smart-watches" element={<SmartWatches />} />
            <Route path="/cameras" element={<Cameras />} />
            <Route path="/laptops" element={<Laptops />} />
            <Route path="/setups" element={<Setups />} />
            <Route path="/contact" element={<ContactUs />} />

            <Route
              path="/cart"
              element={
                <ProtectedRoute>
                  <Cart user={user} />
                </ProtectedRoute>
              }
            />
            <Route
              path="/account"
              element={
                <ProtectedRoute>
                  <Account user={user} onLogout={() => setUser(null)} />
                </ProtectedRoute>
              }
            />

            <Route
              path="/auth"
              element={
                user ? (
                  <Navigate to="/account" />
                ) : (
                  <Auth onLogin={(u) => setUser(u)} />
                )
              }
            />

            <Route path="*" element={<NotFound />} />
          </Routes>
        </div>
        <Footer />
      </HashRouter>
    </div>
  );
}

export default App;
