import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

export default function Navbar() {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 992);
    };
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 0);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <nav
      className={`navbar navbar-expand-lg bg-black bg-opacity-10 fixed-top rounded-bottom-4 ${
        isScrolled ? "py-0" : "py-3"
      } transition-all`}
      style={{ transition: "padding 0.3s ease" }}
    >
      <div className="container-fluid">
        {/* Navbar Brand */}
        <NavLink className="navbar-brand li-element" to="/home">
          Voltix<i className="fa-solid fa-bolt text-warning"></i>
        </NavLink>

        {/* Button For Responsive */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i className="fa-solid fa-bolt m-1 text-warning"></i>
        </button>

        {/* Navbar Elements */}
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul
            className={`navbar-nav me-auto mb-2 mb-lg-0 m-auto d-flex ${
              isMobile
                ? "flex-column align-items-start"
                : "flex-row align-items-center justify-content-center"
            }`}
          >
            <li className="nav-item li-element">
              <NavLink className="nav-link" to="/home">
                Home <i className="fa-solid fa-house text-warning"></i>
              </NavLink>
            </li>

            {isMobile ? (
              <hr className="w-100 my-1" />
            ) : (
              <h1 className="text-black text-opacity-25 mx-2">/</h1>
            )}

            <li className="nav-item li-element">
              <NavLink className="nav-link" to="/products">
                Products{" "}
                <i className="fa-solid fa-cart-shopping text-warning"></i>
              </NavLink>
            </li>

            {isMobile ? (
              <hr className="w-100 my-1" />
            ) : (
              <h1 className="text-black text-opacity-25 mx-2">/</h1>
            )}

            <li className="nav-item li-element">
              <NavLink className="nav-link" to="/cart">
                Cart{" "}
                <i className="fa-solid fa-basket-shopping text-warning"></i>
              </NavLink>
            </li>

            {isMobile ? (
              <hr className="w-100 my-1" />
            ) : (
              <h1 className="text-black text-opacity-25 mx-2">/</h1>
            )}

            <li className="nav-item li-element">
              <NavLink className="nav-link" to="/account">
                Account{" "}
                <i className="fa-solid fa-circle-user fs-4 text-warning"></i>
              </NavLink>
            </li>

            {isMobile ? (
              <hr className="w-100 my-1" />
            ) : (
              <h1 className="text-black text-opacity-25 mx-2">/</h1>
            )}

            <li className="nav-item li-element">
              <NavLink className="nav-link" to="/contact">
                Contact Us{" "}
                <i className="fa-regular fa-address-book text-warning"></i>
              </NavLink>
            </li>
          </ul>

          <form
            className="d-flex"
            role="search"
            style={{ pointerEvents: "none", opacity: 0.8 }}
          >
            <input
              className="form-control me-2"
              type="search"
              placeholder="Still Working On Search Input"
              aria-label="Search"
            />
            <button className="btn btn-warning" type="submit">
              <i className="fa-brands fa-searchengin fs-4"></i>
            </button>
          </form>
        </div>
      </div>
    </nav>
  );
}
