import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

export default function Footer() {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 992);
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const Separator = () =>
    isMobile ? (
      <hr className="w-100 my-1" />
    ) : (
      <span className="text-black text-opacity-25 mx-2">/</span>
    );

  return (
    <footer className="container-fluid bg-dark py-4 rounded-top-5">
      <div className="row g-3">
        {/* Left Section - About & Links */}
        <div className="col-sm-6">
          <div className="card bg-black bg-opacity-25 text-light border-0 shadow-sm h-100">
            <div className="card-body">
              <h5 className="card-title fw-bold text-warning mb-3">
                Voltix <i className="fa-solid fa-bolt text-warning"></i>
              </h5>
              <p className="card-text text-secondary">
                Our goal is to provide the{" "}
                <span className="text-warning">best service</span> to our
                customers.
              </p>
              <ul
                className={`navbar-nav me-auto mb-2 mb-lg-0 m-auto d-flex ${
                  isMobile
                    ? "flex-column align-items-start"
                    : "flex-row align-items-center justify-content-center"
                }`}
              >
                <li className="nav-item li-element">
                  <NavLink className="nav-link" to="/home">
                    Home <i className="fa-solid fa-house text-warning"></i>
                  </NavLink>
                </li>

                {isMobile ? (
                  <hr className="w-100 my-1" />
                ) : (
                  <h1 className="text-warning text-opacity-25 mx-2">/</h1>
                )}

                <li className="nav-item li-element">
                  <NavLink className="nav-link" to="/products">
                    Products{" "}
                    <i className="fa-solid fa-cart-shopping text-warning"></i>
                  </NavLink>
                </li>

                {isMobile ? (
                  <hr className="w-100 my-1" />
                ) : (
                  <h1 className="text-warning text-opacity-25 mx-2">/</h1>
                )}

                <li className="nav-item li-element">
                  <NavLink className="nav-link" to="/cart">
                    Cart{" "}
                    <i className="fa-solid fa-basket-shopping text-warning"></i>
                  </NavLink>
                </li>

                {isMobile ? (
                  <hr className="w-100 my-1" />
                ) : (
                  <h1 className="text-warning text-opacity-25 mx-2">/</h1>
                )}

                <li className="nav-item li-element">
                  <NavLink className="nav-link" to="/account">
                    Account{" "}
                    <i className="fa-solid fa-circle-user fs-4 text-warning"></i>
                  </NavLink>
                </li>

                {isMobile ? (
                  <hr className="w-100 my-1" />
                ) : (
                  <h1 className="text-warning text-opacity-25 mx-2">/</h1>
                )}

                <li className="nav-item li-element">
                  <NavLink className="nav-link" to="/contact">
                    Contact Us{" "}
                    <i className="fa-regular fa-address-book text-warning"></i>
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
        </div>

        {/* Right Section - Contact Info */}
        <div className="col-sm-6">
          <div className="card bg-black bg-opacity-25 text-light border-0 shadow-sm h-100">
            <div className="card-body">
              <h5 className="card-title fw-bold text-warning mb-3">
                Contact Us <i className="fa-solid fa-bolt text-warning"></i>
              </h5>

              {/* WhatsApp Link */}
              <p className="card-text mb-2">
                <a
                  href="https://wa.me/201095554022"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-decoration-none text-warning"
                >
                  <i className="fa-brands fa-whatsapp text-success"></i> +2 010
                  9555 4022
                </a>
              </p>

              {/* Email Link */}
              <p className="card-text mb-3">
                <a
                  href="mailto:abo.al.magd.404@gmail.com"
                  className="text-decoration-none text-warning"
                >
                  <i className="fa-solid fa-envelope text-danger"></i>{" "}
                  abo.al.magd.404@gmail.com
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Footer bottom */}
      <h6 className="text-center text-secondary mt-3 mb-0 fs-5">
        <span className="text-danger">&copy;</span> 2025 : All Rights Reserved{" "}
        <br />
        Made With <i className="fa-solid fa-heart text-danger"></i> By{" "}
        <span className="text-warning">Abo-Al-Magd</span>
      </h6>
    </footer>
  );
}
